.banner-cover {
  width: 100%;
  max-height: 550px;
  object-fit: cover;
}
.banner {
  position: relative;
}
.banner-title {
  font-size: 40px;
}

.banner-contents {
  color: white;
  position: absolute;
  bottom: 180px;
  left: 50px;
}
.banner-button {
  margin-right: 10px;
  height: 40px;
}

.description {
  font-size: 20px;
  width: 50%;
  margin-top: 20px;
}

h1 {
  color: white;
}
